.footer {
    background-color: var(--color-bg-01);
    padding: 24px 0;

    @media screen and (min-width: $w1440) {
        padding: 92px 0 34px;
    }

    @media screen and (min-width: $w1200) {
        padding: 64px 0 32px;
    }

    & .container {
        @media screen and (min-width: $w992) {
            display: flex;
        }
    }

    &__col {
        @media screen and (min-width: $w992) {
            flex-grow: 1;
            width: 33.33%;

            &--menu {
                padding-left: 16px;
            }

            &--menu,
            &--left {
                padding-right: 32px;

                @media screen and (min-width: $w1200) {
                    padding-right: 48px;
                }
            }

            &--contacts {

                @media screen and (min-width: $w1440) {
                    padding-left: 32px;
                }

                & .footer {
                    &__link {
                        @media screen and (min-width: $w1440) {
                            padding-bottom: 32px;
                        }
                    }
                }
            }
        }
    }

    &__logo {

        padding-bottom: 24px;

        @media screen and (min-width: $w1440) {
            padding-bottom: 46px;
        }

        & span {
            width: 156px;
            height: 20px;
            @include bgimage-contain;
        }
    }

    &__link {
        padding-bottom: 28px;

        @media screen and (min-width: $w1440) {
            padding-bottom: 52px;
        }
    }

    &__title {
        font-size: 18px;
        font-weight: bold;
        padding-bottom: 18px;

        @media screen and (min-width: $w1200) {
            @include font-h4
        }
    }

    &__menu {
        display: none;
        padding-bottom: 12px;

        @media screen and (min-width: $w1440) {
            padding-bottom: 36px;
        }

        & ul {
            & li {
                padding: 5px 0;
                transition: 0.3s ease;

                @media screen and (min-width: $w1440) {
                    padding: 8px 0;
                }

                &:hover {
                    transform: translateX(10px);

                    & a {
                        color: #fff;
                    }
                }

                & a {
                    color: var(--color-text-02);

                    @media screen and (min-width: $w1440) {
                        font-size: 18px;
                    }
                }
            }
        }


        @media screen and (min-width: $w992) {
            display: block;
        }
    }


    &__msgr {

        padding-bottom: 21px;

        @media screen and (min-width: $w992) {
            padding-bottom: 14px;
        }

        & ul {
            display: flex;
            margin: -8px;
            justify-content: flex-start;



            & .bgimage {
                width: 24px;
                height: 24px;
                fill: #fff;
                display: block;
                @include bgimage-contain;

                &:hover {
                    filter: brightness(400) grayscale(1);
                }
            }

            & li {
                padding: 8px;

                & a {
                    display: block;
                    height: 24px;
                    width: 24px;
                    position: relative;
                }



            }
        }
    }
}

.footer-link {
    &__label {
        font-size: 14px;
        color: var(--color-text-02);

        @media screen and (min-width: $w1440) {
            font-size: 15px;
            padding-bottom: 6px;
        }
    }

    &__a {
        & a {
            font-size: 16px;
            font-weight: bold;

            @media screen and (min-width: $w1440) {
                font-size: 18px;
            }
        }
    }
}

.footer-bottom {
    background-color: var(--color-bg-02);
    padding: 18px 0;

    & .container {
        @media screen and (min-width: $w1200) {
            display: flex;
            align-items: center;
        }
    }

    &__info {
        @media screen and (min-width: $w1200) {
            flex-grow: 1;
        }
    }

    &__company {
        padding-bottom: 12px;
        font-size: 16px;
        color: var(--color-text-01);

        & strong {
            display: block;
            font-weight: bold;
            padding-bottom: 14px;

            @media screen and (min-width: $w1200) {
                display: inline;
                margin-right: 10px;
            }
        }
    }

    &__copyright {
        font-size: 14px;
        color: var(--color-text-02);
        padding-bottom: 24px;
        line-height: 143%;

        @media screen and (min-width: $w1200) {
            padding-bottom: 0;
        }
    }

    &__dev {
        color: var(--color-text-02);

        @media screen and (min-width: $w1440) {
            display: flex;
            align-items: center;

            & span {
                padding-bottom: 0;
                padding-right: 10px;
            }
        }

        & span {
            display: block;
            padding-bottom: 12px;

            @media screen and (min-width: $w1440) {
                display: inline;
            }
        }

        & img {
            max-width: 208px;
        }
    }
}